import moment from 'moment'
import JsonViewer from '@/components/JsonViewer'
import { required, minValue } from 'vuelidate/lib/validators'
import VueNumeric from 'vue-numeric'
export default {
    name: 'loan-a-create',
    data() {
        return {
            loan: {
                amount: '',
                repaymentDate: ''
            },
            errorMessage: ''
        }
    },
    validations: {
        loan: {
            amount: { 
                required, 
                minValue: minValue(100000)
            }
        }
    },
    components: {
        JsonViewer,
        VueNumeric
    },
    methods: {
        handleSubmit() {
            this.$v.$touch()
            if (this.$v.$invalid) {
                this.errorMessage = 'Please complete mandatory fields'
            } else {
                this.$bvModal.show('modal-confirmation')
            }
        },
        handleModalConfirmationOk() {
            this.$bvModal.show('modal-loading')
            let now = Date.now()
            setTimeout(() => {
                this.$http({
                    method: 'post',
                    url: '/loan/manage-loan/loan-a',
                    headers: {
                        Authorization: 'Bearer ' + this.$store.getters.getToken
                    },
                    data: {
                        id_employee: this.$store.getters.getUid,
                        loan_type: 'a',
                        loan_amount: this.loan.amount,
                        created_date: moment(now).format('YYYY-MM-DD'),
                        repayment_date: moment(this.loan.repaymentDate).format('YYYY-MM-DD')
                    }
                })
                .then(response => {
                    console.log(response.data)
                    this.$bvModal.hide('modal-loading')
                    response.data.response_code == '01' ? this.$router.push('/loan-a') : this.errorMessage = response.data.response
                })
                .catch(error => {
                    console.log(error.response.data)
                    this.$bvModal.hide('modal-loading')
                })
            }, 500)
        },
        resetErrorMessage() {
            this.errorMessage = ''
        },
        getEndOfMonth(_date) {
            // Initiate repayment date to be the end of the month
            return moment(new Date(_date.getFullYear(), _date.getMonth() + 1, 0)).format('YYYY-MM-DD')
        }
    },
    created() {
        this.loan.repaymentDate = this.getEndOfMonth(new Date())
    }
}